body{
  background-color: #c2c2c2;
}
.wrap{
  flex-wrap: wrap;
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  border: 2px solid rgb(0, 0, 0);
  background-color: #fff;
  margin: 3rem  auto;
  align-items: center;
}
.wrap .item{
  width: 160px;
  border: 2px solid rgb(0, 0, 0);
  height: 150px;
  padding: 30px;
  background-color: #fff;
  border-radius:10%;
}